import React from "react";
import * as Compo from "../components";

export const giveBadge = (is_verified, audience_number) => {
    const NumAbbr = require('number-abbreviate');
    const numAbbr = new NumAbbr();

    return (
        <>
            <span className={'mr-1 text-uppercase'}>{numAbbr.abbreviate(audience_number)}</span>
            {
                is_verified &&
                <Compo.Icon type={'fas'} icon={'badge-check'}/>
            }
        </>
    )
}

export const range = (length) => {
    return Array.from({ length: length})
}

export const makeChunk = (data, chunk) => {
    return data.map((e, i) => {
        return i % chunk === 0 ? data.slice(i, i + chunk) : null;
    }).filter(e => { return e; });
}