import React from "react";
import {Nav, Col, Row} from "react-bootstrap";
import {LinkContainer} from 'react-router-bootstrap'

const Footer = () => {
    return (
        <div className={'footer'}>
            <div className="container py-2">
                <Row className={'align-items-center'}>
                    <Col md={8}>
                        <Nav className="mr-auto">
                            <LinkContainer to={'professionals'}>
                                <Nav.Link href="/professionals" className={'footer-nav-link'}>About Us</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to={'professionals'}>
                                <Nav.Link href="/gears" className={'footer-nav-link'}>Career</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to={'professionals'}>
                                <Nav.Link href="/locations-studios" className={'footer-nav-link'}>Terms</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to={'professionals'}>
                                <Nav.Link href="/jobs" className={'footer-nav-link'}>Content Policy</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to={'professionals'}>
                                <Nav.Link href="/jobs" className={'footer-nav-link'}>Privacy Policy</Nav.Link>
                            </LinkContainer>
                        </Nav>
                    </Col>
                    <Col md={4}>
                        <p className={'mb-0 text-right'}>Indiafilmdirectory 2021. All rights reserved</p>
                    </Col>
                </Row>
            </div>
        </div>
    )
}


export default Footer
