import React from "react";
import * as Compo from "../../../../components";
import {Col, Row} from "react-bootstrap";

class General extends React.Component {

    render () {
        return (
            <>
                <Row>
                    <Col md={6}>
                        <iframe
                            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d241317.11609823277!2d72.74109995709657!3d19.08219783958221!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3be7c6306644edc1%3A0x5da4ed8f8d648c69!2sMumbai%2C%20Maharashtra%2C%20India!5e0!3m2!1sen!2sbd!4v1623448937151!5m2!1sen!2sbd"
                            width="100%"
                            height="100%"
                            style={{
                                border: 'none'
                            }}
                            loading="lazy"
                            title={'State Location'}
                        />
                    </Col>
                    <Col md={6}>
                        <Row className={'color-gray'}>
                            <Col md={4}>
                                <h4>Tiar</h4>
                                <p>2</p>
                            </Col>
                            <Col md={4}>
                                <h4>Number of locations</h4>
                                <p>30,000 locations</p>
                            </Col>
                            <Col md={4}>
                                <h4>Number of studios</h4>
                                <p>10,000 studios</p>
                            </Col>

                            <Col md={12} className={'mt-4'}>
                                <h4>Languages</h4>
                                <p>Hindi, English, Tamil, Bangali, Punjabi</p>
                            </Col>

                            <Col md={12} className={'mt-4'}>
                                <h4>Geographic</h4>
                                <ul className={'inline-list'}>
                                    <li>Sandy Plains</li>
                                    <li>Aravali Plains</li>
                                    <li>Eastern Plains</li>
                                    <li>South-Eastern</li>
                                    <li>Rajasthan Pathar</li>
                                </ul>
                            </Col>

                            <Col md={12} className={'mt-4'}>
                                <h4>Top Locations</h4>
                                <ul className={'inline-list'}>
                                    <li>Sandy Plains</li>
                                    <li>Aravali Plains</li>
                                    <li>Eastern Plains</li>
                                    <li>South-Eastern</li>
                                    <li>Rajasthan Pathar</li>
                                    <li>Sandy Plains</li>
                                    <li>Aravali Plains</li>
                                    <li>Eastern Plains</li>
                                    <li>South-Eastern</li>
                                </ul>
                            </Col>

                            <Col md={12}>
                                <hr/>
                            </Col>

                            <Col md={12}>
                                <Row>
                                    <Col md={12} className={'text-center'}>
                                        <p className={'mb-3'}>Have some question related to the state? We will be happy to answer.</p>
                                    </Col>
                                    <Col md={6}>
                                        <Compo.Button
                                            variant={'primary'}
                                            block
                                        >
                                            Mail Us
                                        </Compo.Button>
                                    </Col>
                                    <Col md={6}>
                                        <Compo.Button
                                            variant={'outline-primary'}
                                            block
                                        >
                                            Call Us
                                        </Compo.Button>
                                    </Col>
                                </Row>
                            </Col>

                        </Row>
                    </Col>
                </Row>
            </>
        )
    }
}

export default General
