import React from "react";

// Helper Functions
import {range} from "../../../../helpers/utility";

// Custom Components
import * as Compo from "../../../../components";

// React Bootstrap Elements
import {Card, Image, Media} from "react-bootstrap";

// Dropdown Menu Elements
import {default as DrpDown} from "react-bootstrap/Dropdown";
import Dropdown from "../../../../components/compo/Dropdown";

class Comments extends React.Component {

    render () {
        return (
            <>
                {
                    range(6).map(i => {
                        return (
                            <Media className="border-bottom p-3">
                                <Image src={'https://i.pinimg.com/originals/51/f6/fb/51f6fb256629fc755b8870c801092942.png'} roundedCircle thumbnail fluid width={60} className={'mr-3 shadow'}/>
                                <Media.Body>
                                    <div className={'d-flex'}>
                                        <span>
                                            <h4 className={'mb-0'}>
                                                User Name • <span className={'color-mariner'}>5M <Compo.Icon type={'fas'} icon={'badge-check'}/></span>
                                            </h4>
                                            <p>Position</p>
                                            <p>10 min</p>
                                        </span>
                                        <div className={'ml-auto'}>
                                            <Dropdown
                                                actions={[
                                                    {name: 'Show', action: () => this.alertOne('Show')},
                                                    {name: 'Hide', action: () => this.alertOne('Hide')}
                                                ]}
                                            >
                                                <Compo.Icon
                                                    type={'fas'}
                                                    icon={'ellipsis-v'}
                                                />

                                                <DrpDown.Menu>
                                                    <DrpDown.Item onClick={() => alert('Show')}>Show</DrpDown.Item>
                                                    <DrpDown.Item onClick={() => alert('Hide')}>Hide</DrpDown.Item>
                                                </DrpDown.Menu>
                                            </Dropdown>
                                        </div>
                                    </div>
                                    <p className={'font-size-14 color-gray mt-3 mb-4'}>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusantium culpa cupiditate debitis distinctio dolor explicabo illo ipsa ipsum iste labore molestias mollitia natus necessitatibus nesciunt perspiciatis provident sed, sint soluta!</p>
                                    <div className={'text-right'}>
                                        <span className={'font-size-14 color-gray mr-3'}>Found this review helpful?</span>
                                        <Compo.Button
                                            variant={'link'}
                                            style={{
                                                "font-size": "var(--font-size-14)",
                                                "padding": '0',
                                                "color": 'var(--gray)'
                                            }}
                                        >
                                            <span className={'color-mariner font-weight-700 mr-3'}>
                                                <Compo.Icon type={'fas'} icon={'thumbs-up'} className={'mr-2'} size={'lg'}/>
                                                <span>12.4K</span>
                                            </span>
                                        </Compo.Button>
                                        <Compo.Button
                                            variant={'link'}
                                            style={{
                                                "font-size": "var(--font-size-14)",
                                                "padding": '0',
                                                "color": 'var(--gray)'
                                            }}
                                        >
                                            <span className={'color-gray font-weight-700 mr-3'}>
                                                <Compo.Icon type={'fas'} icon={'thumbs-down'} className={'mr-2'} size={'lg'}/>
                                                <span>0</span>
                                            </span>
                                        </Compo.Button>
                                    </div>
                                </Media.Body>
                            </Media>
                        )
                    })
                }

                <Card.Footer className={'text-center border-top-0 bg-transparent'}>
                    <Compo.Button variant={'outline-primary'}>
                        <span className={'mr-2'}>
                            <Compo.Icon type={'fas'} icon={'plus'}/>
                        </span>
                        Load More
                    </Compo.Button>
                </Card.Footer>
            </>
        )
    }
}


export default Comments
