import React from "react";
import {Modal} from "react-bootstrap";
import {closeModal, initModals} from "../../../../services/actions/modal";
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import * as Compo from "../../../../components/index";
import {Field, reduxForm} from "redux-form";


class CompleteProfile extends React.Component {
    render () {
        const { handleSubmit, submitting, pristine} = this.props;
        return (
            <Modal
                show={this.props.modal.show}
                onHide={() => this.props.closeModal('complete-profile')}
                backdrop={'static'}
                className={'auth-modal'}
                centered
            >
                <Modal.Header closeButton>
                    <p className={'mb-0'}>Welcome To Nineoneconnect</p>
                    <Modal.Title>Complete Profile</Modal.Title>
                    <p className={'mb-0'}>By continuing, you agree to our <Link>User Agreement</Link> and <Link>Privacy Policy</Link></p>
                </Modal.Header>
                <Modal.Body>
                    <form onSubmit={handleSubmit}>
                        <Field
                            name="country"
                            component={Compo.Field.Select}
                            placeholder="Select Country"
                            label={"Country"}
                            isClearable={true}
                            options={this.props?.all_countries}
                            option_label_key={'name'}
                            option_value_key={'id'}
                        />

                        <Field
                            name="type"
                            component={Compo.Field.Select}
                            placeholder="Select Account Type"
                            label={"Account Type"}
                            isClearable={true}
                            options={[
                                {label: 'Individual', value: 'individual'},
                                {label: 'Organization', value: 'organization'}
                            ]}
                        />

                        {
                            (!this.props.auth.user.email || this.props.auth.user.email === `${this.props.auth.user.username}@filmdirectory.in`) &&
                                <Field
                                    name="email"
                                    component={Compo.Field.Input}
                                    placeholder="Select Account Type"
                                    label={"Email"}
                                />
                        }

                        <Field
                            name="username"
                            component={Compo.Field.Input}
                            placeholder="Enter Your Username"
                            label={"Username"}
                        />

                        <Field
                            name="password"
                            component={Compo.Field.Input}
                            placeholder="Enter Your Password"
                            label={"Password"}
                            type={'password'}
                        />

                        <Field
                            name="confirm_password"
                            component={Compo.Field.Input}
                            placeholder="Enter Your Password Again"
                            label={"Confirm Password"}
                            type={'password'}
                        />

                        <Compo.Button
                            className={'mb-4 mt-4'}
                            type={'submit'}
                            is_loading={submitting}
                            disabled={pristine || submitting}
                            block
                        >
                            Save Info
                        </Compo.Button>
                    </form>
                </Modal.Body>
                <Modal.Footer className={'text-center'}>
                    <span className={'mr-2 font-size-14'}>New to Nineoneconnect?</span>
                    <Compo.Button
                        variant={'link'}
                        style={{
                            "padding": "0",
                            "margin": "0",
                            "font-size": "14px"
                        }}
                    >
                        SIGNUP
                    </Compo.Button>
                </Modal.Footer>
            </Modal>
        )
    }
}

CompleteProfile = reduxForm({
    form: 'complete-profile'
})(CompleteProfile)


const stateToProps = (state) => ({
    auth: state.auth,
    modal: state.modal['complete-profile'],
    all_countries : state.data['all_countries']?.dataset?.data
})

const dispatchToProps = (dispatch) => ({
    initModals: (names) => dispatch(initModals(names)),
    closeModal: (name) => dispatch(closeModal(name))
})

export default connect(stateToProps, dispatchToProps)(CompleteProfile)
