import Axios from "axios";

const get_headers = () => {
    const session = JSON.parse(localStorage.getItem('session'))
    const referrer = JSON.parse((localStorage.getItem('referrer')))
    return {
        "auth-key": session?.token || undefined,
        "referrer-id": referrer || undefined
    }
}

const make_url = (host, endpoint) => {
    return `http://${host}/` + endpoint
}

export const get = (host, endpoint) => {
    return Axios.get(make_url(host, endpoint),{
        headers: get_headers()
    }).then(res => {
        return res.data
    }).catch(error => {
        return error.response?.data
    })
}

export const post = (host, endpoint, data) => {
    return Axios.post(make_url(host, endpoint), data,{
        headers: get_headers()
    }).then(res => {
        return res.data
    }).catch(error => {
        return error.response?.data
    })
}
