import 'react-toastify/dist/ReactToastify.css';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import './assets/icons/fontawesome/css/all.css'
import './assets/styles.css'

import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import {Provider} from 'react-redux'
import store from "./services/store";


import MainLayout from "./views/layouts/MainLayout";
import Home from "./views/pages/Home";
import Search from "./views/pages/Search";
import SingleProject from "./views/pages/single/Project";
import { LinkedInPopUp } from 'react-linkedin-login-oauth2';
import SingleState from "./views/pages/single/State";
import SingleCity from "./views/pages/single/City";
import SingleJob from "./views/pages/single/Job";
import SingleStudio from "./views/pages/single/Studio";
import SingleGear from "./views/pages/single/Gear";


function App() {

  return (
      <Provider store={store}>
          <Router>
              <Switch>
                  <Route path={'/'} exact>
                      <MainLayout>
                          <Home/>
                      </MainLayout>
                  </Route>
                  <Route path={'/auth/login/linkedin'}>
                      <LinkedInPopUp/>
                  </Route>
                  <Route path={'/search/:type/:query?'}>
                      <MainLayout>
                          <Search/>
                      </MainLayout>
                  </Route>
                  <Route path={'/project/:slug'}>
                      <MainLayout search={true}>
                          <SingleProject/>
                      </MainLayout>
                  </Route>
                  <Route path={'/state/:slug'}>
                      <MainLayout>
                          <SingleState/>
                      </MainLayout>
                  </Route>
                  <Route path={'/city/:slug'}>
                      <MainLayout>
                          <SingleCity/>
                      </MainLayout>
                  </Route>
                  <Route path={'/job/:slug'}>
                      <MainLayout>
                          <SingleJob/>
                      </MainLayout>
                  </Route>
                  <Route path={'/studio/:slug'}>
                      <MainLayout>
                          <SingleStudio/>
                      </MainLayout>
                  </Route>
                  <Route path={'/gear/:slug'}>
                      <MainLayout>
                          <SingleGear/>
                      </MainLayout>
                  </Route>
              </Switch>
          </Router>
      </Provider>
  );
}

export default App;
