import React from "react";
import {Badge, Card, Dropdown} from "react-bootstrap";
import * as Compo from "../../../components/index";
import {giveBadge} from "../../../helpers/utility";
import {LinkContainer} from "react-router-bootstrap";
import Moment from "react-moment";

class StudioCard extends React.Component {

    render () {
        return (
            <Card className={'item-card'}>
                <Compo.Slider
                    dots={true}
                >
                    {
                        this.props.item.images.map(image => (
                            <img
                                src={image}
                                alt="First slide"
                            />
                        ))
                    }
                </Compo.Slider>
                <Card.Header>
                    <div className="row d-flex justify-content-between">
                        <div>
                            <Compo.Button
                                variant={'link'}
                                style={{
                                    "padding": '0',
                                    "color": 'var(--gray)'
                                }}
                            >
                                <Compo.Icon
                                    type={'far'}
                                    icon={'bookmark'}
                                />
                            </Compo.Button>
                        </div>
                        <div>
                            <Compo.Dropdown>
                                <Compo.Icon
                                    type={'fas'}
                                    icon={'ellipsis-v'}
                                />

                                <Dropdown.Menu>
                                    <Dropdown.Item onClick={() => alert('Show')}>
                                        View
                                    </Dropdown.Item>
                                    <Dropdown.Item onClick={() => alert('Hide')}>
                                        Report
                                    </Dropdown.Item>
                                </Dropdown.Menu>
                            </Compo.Dropdown>
                        </div>
                    </div>
                </Card.Header>
                <Card.Body>
                    <div className="d-flex align-items-center mb-1">
                        <p className={'card-subtitle'}>
                            <Compo.Text limit={18} tail={'..'}>
                                Location
                            </Compo.Text>
                            <span className={'mx-1'}>•</span>
                            <span>
                                {this.props.item.projects} Projects
                            </span>
                        </p>
                    </div>
                    <LinkContainer to={`/p/${this.props.item.id}`}>
                        <h3 className={'mt-2 mb-0 cursor-pointer'}>{this.props.item.name}</h3>
                    </LinkContainer>
                    <p className={'color-mariner'}>
                        {
                            giveBadge(this.props.item.is_verified, this.props.item.followers)
                        }
                    </p>
                    <p className={'card-text'}>
                        <Moment format={'DD MMMM YYYY'}>{this.props.item.created_at}</Moment>
                    </p>
                </Card.Body>
                <Card.Footer>
                    <Badge variant={'primary'}>Tag 1</Badge>
                    <Badge variant={'primary'}>Tag 2</Badge>
                    <Badge variant={'primary'}>Tag 2</Badge>
                    <Badge variant={'light'}>32+</Badge>
                </Card.Footer>
            </Card>
        )
    }
}


export default StudioCard
