import {
    set_session,
    reset_session,
} from "../constants";

import {post} from "../../helpers/api";
import {closeModal, openModal} from "./modal";
import { toast } from 'react-toastify';
import {user_service_host} from "../../helpers/configs";

export const login = (data, callback) => {
    localStorage.setItem('session', JSON.stringify(data))

    if (callback instanceof Function) {
        callback(data)
    }

    return {
        type: set_session,
        data: data
    }
}

export const facebookLogin = (data, callback=undefined) => {
    return function(dispatch) {
        const formData = new FormData()

        if (!data.accessToken || ! data.userID) {
            toast['error']('Can\'t Login With Facebook Now, Please Try Again Later.')

            dispatch(closeModal('login'))
            dispatch(closeModal('registration'))
        } else {
            formData.append('access_token', data.accessToken)
            formData.append('user_id', data.userID)

            post(user_service_host(),'auth/client/login/facebook', formData).then(result => {
                localStorage.setItem('session', JSON.stringify(result.data))

                dispatch({
                    type: set_session,
                    data: result.data
                })

                dispatch(closeModal('login'))
                dispatch(closeModal('registration'))

                const user = result.data.user
                if (!user.type){
                    dispatch(openModal('complete-profile'))
                }

                toast[result.status](result.message)

                if (callback instanceof Function) {
                    callback(result.data)
                }
            })
        }
    }
}


export const linkedinLogin = (data, callback = undefined) => {
    return function(dispatch) {
        const formData = new FormData()

        formData.append('code', data.code)

        post(user_service_host(),'auth/client/login/linkedin', formData).then(result => {
            localStorage.setItem('session', JSON.stringify(result.data))

            dispatch({
                type: set_session,
                data: result.data
            })

            dispatch(closeModal('login'))
            dispatch(closeModal('registration'))

            const user = result.data.user
            if (!user.type){
                dispatch(openModal('complete-profile'))
            }

            toast[result.status](result.message)

            if (callback instanceof Function) {
                callback(result.data)
            }
        })
    }
}


export const googleLogin = (data, callback = undefined) => {
    return function(dispatch) {
        const formData = new FormData()

        formData.append('name', data.profileObj.name)
        formData.append('email', data.profileObj.email)
        formData.append('user_id', data.profileObj.googleId)
        formData.append('access_token', data.accessToken)

        post(user_service_host(),'auth/client/login/google', formData).then(result => {
            if (result.status_code === 200) {
                localStorage.setItem('session', JSON.stringify(result.data))

                dispatch({
                    type: set_session,
                    data: result.data
                })
            }

            dispatch(closeModal('login'))
            dispatch(closeModal('registration'))

            const user = result.data.user
            if (!user.type){
                dispatch(openModal('complete-profile'))
            }

            toast[result.status](result.message)

            if (callback instanceof Function) {
                callback(result.data)
            }
        })
    }
}


export const logout = () => {
    return function(dispatch) {
        localStorage.removeItem('session')
        dispatch({
            type: reset_session
        })
    }
}
