import React, { Component } from "react";
import {default as ReactSlider} from "react-slick";
import {SRLWrapper} from "simple-react-lightbox";

export default class Slider extends Component {
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
        this.state = {
            mainSlider: null,
            thumbnailSlider: null,
            activeSlide: 0
        };
    }

    next() {
        this.slider1.slickNext();
    }

    previous() {
        this.slider1.slickPrev();
    }

    componentDidMount() {
        this.setState({
            mainSlider: this.slider1,
            thumbnailSlider: this.slider2
        });
    }

    render() {
        return (
            <div>

                {
                    this.props.lightbox?
                        <SRLWrapper>
                            <ReactSlider
                                arrows={false}
                                dots={false}
                                asNavFor={this.state.thumbnailSlider}
                                ref={slider => (this.slider1 = slider)}
                                beforeChange={(current, next) => this.setState({ activeSlide: next })}
                                afterChange={current => this.setState({ activeSlide2: current })}
                                className={this.props.className}
                                infinite={false}
                            >
                                {
                                    this.props.children
                                }
                            </ReactSlider>
                        </SRLWrapper>
                        :
                        <ReactSlider
                            arrows={false}
                            dots={false}
                            asNavFor={this.state.thumbnailSlider}
                            ref={slider => (this.slider1 = slider)}
                            beforeChange={(current, next) => this.setState({ activeSlide: next })}
                            afterChange={current => this.setState({ activeSlide2: current })}
                            className={this.props.className}
                            infinite={false}
                        >
                            {
                                this.props.children
                            }
                        </ReactSlider>
                }

                {
                    this.props.thumbnail &&
                        <ReactSlider
                            asNavFor={this.state.mainSlider}
                            ref={slider => (this.slider2 = slider)}
                            slidesToShow={4}
                            swipeToSlide={true}
                            focusOnSelect={true}
                            dots={false}
                            arrows={false}
                            className={'mt-3'}
                            infinite={false}
                        >
                                {
                                    this.props.children
                                }
                        </ReactSlider>
                }

                <ol className={`carousel-indicators`}>
                    {
                        this.props.arrows &&
                            <i className="fas fa-caret-circle-left" onClick={this.previous}/>
                    }
                    {
                        this.props.dots && Array.from({ length: this.slider1?.props?.children?.length}, (v, i) =>  {
                            return (
                                <li
                                    className={this.state.activeSlide === i && 'active'}
                                    onClick={() => this.slider1.slickGoTo(i)}
                                    key={i}
                                />
                            )
                        })
                    }
                    {
                        this.props.arrows &&
                            <i className="fas fa-caret-circle-right" onClick={this.next}/>
                    }
                </ol>
            </div>
        );
    }
}
