import React from "react";
import * as Compo from "../../../../components";
import {Card, Image, Media} from "react-bootstrap";
import {range} from "../../../../helpers/utility";

class Gears extends React.Component {

    render () {
        return (
            <>
                {
                    range(6).map(i => {
                        return (
                            <Media className="border-bottom p-3">
                                <Image src={'https://i.pinimg.com/originals/51/f6/fb/51f6fb256629fc755b8870c801092942.png'} roundedCircle thumbnail fluid width={60} className={'mr-3 shadow'}/>
                                <Media.Body>
                                    <div className={'d-flex'}>
                                        <span>
                                            <h4 className={'mb-1'}>
                                                Gear Name
                                            </h4>
                                            <p>Vendor Name • <span className={'color-mariner'}>5M <Compo.Icon type={'fas'} icon={'badge-check'}/></span></p>
                                            <p className={'mb-0 font-size-14 color-gray'}>Location</p>
                                        </span>
                                        <div className={'ml-auto'}>
                                            <Compo.Button
                                                variant={'link'}
                                                style={{
                                                    padding: 0,
                                                    fontSize: 'var(--font-size-14)'
                                                }}
                                            >
                                                <span className={'mr-3'}>View</span>
                                                <Compo.Icon type={'fas'} icon={'angle-right'}/>
                                            </Compo.Button>
                                        </div>
                                    </div>
                                </Media.Body>
                            </Media>
                        )
                    })
                }

                <Card.Footer className={'text-center border-top-0 bg-transparent'}>
                    <Compo.Button variant={'outline-primary'}>
                        <span className={'mr-2'}>
                            <Compo.Icon type={'fas'} icon={'plus'}/>
                        </span>
                        Load More
                    </Compo.Button>
                </Card.Footer>
            </>
        )
    }
}


export default Gears
