import React from "react";
import {FormControl, InputGroup} from "react-bootstrap";
import * as Compo from "../../components";


const MainSearchArea = () => {
    return (
        <div className={'container'}>
            <div className={'my-4'}>
                <InputGroup>
                    <InputGroup.Prepend>
                        <InputGroup.Text className={'main-search-input-icon'}>
                            <Compo.Icon type={'fa'} icon={'search'}/>
                        </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl placeholder="Search For Professionals, Gears, Location and Jobs" className={'main-search-input'} size={'lg'}/>
                </InputGroup>
            </div>
        </div>
    )
}


export default MainSearchArea
