import React from "react";
import * as Compo from "../../components";
import {Nav, Tab} from "react-bootstrap";
import SearchProject from "./search/SearchProject";

class Search extends React.Component {

    render () {
        return (
            <>
                <Compo.MainSearchArea className={'py-3'}/>

                <div className="primary-tab">
                    <Tab.Container defaultActiveKey="project">
                        <Nav variant="tabs">
                            <div className="container d-flex">
                                <Nav.Item>
                                    <Nav.Link eventKey="project">project</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="gear">gear</Nav.Link>
                                </Nav.Item>
                            </div>
                        </Nav>
                        <Tab.Content>
                            <Tab.Pane eventKey="project">
                                <SearchProject/>
                            </Tab.Pane>
                            <Tab.Pane eventKey="gear">
                                <h1>Gears</h1>
                            </Tab.Pane>
                        </Tab.Content>
                    </Tab.Container>
                </div>
            </>
        )
    }
}


export default Search
