import React from "react";
import {Card, Col, Row} from "react-bootstrap";
import * as Compo from "../../../../components";
import {range} from "../../../../helpers/utility";

class Transportation extends React.Component {

    render () {
        return (
            <>
                <Row className={'color-gray'}>
                    <Col md={12}>
                        <h2 className={'font-weight-700 mb-5'}>Transportation near the location</h2>
                    </Col>
                    <Col md={12} className={'mb-5'}>
                        <h4>Airport</h4>
                        <Row>
                            {
                                range(3).map(index => (
                                    <Col md={4} className={'mb-3'}>
                                        <Card>
                                            <Card.Body className={'p-3'}>
                                                <Row>
                                                    <Col md={5}>
                                                        <h5 className={'color-initial mb-1'}>Name</h5>
                                                        <p className={'mb-0 font-size-14'}>Location • Type</p>
                                                    </Col>
                                                    <Col md={7} className={'d-flex justify-content-between font-weight-700 align-items-end'}>
                                                        <div className={'text-center px-2'}>
                                                            <Compo.Icon type={'fal'} icon={'globe'}/>
                                                            <p className={'font-size-12 mb-0'}>Visit Website</p>
                                                        </div>
                                                        <div className={'text-center px-2'}>
                                                            <Compo.Icon type={'fal'} icon={'map-marked'}/>
                                                            <p className={'font-size-12 mb-0'}>Open Map</p>
                                                        </div>
                                                    </Col>
                                                </Row>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                ))
                            }
                        </Row>
                    </Col>
                    <Col md={12} className={'mb-5'}>
                        <h4>Train Stations</h4>
                        <Row>
                            {
                                range(6).map(index => (
                                    <Col md={4} className={'mb-3'}>
                                        <Card>
                                            <Card.Body className={'p-3'}>
                                                <Row>
                                                    <Col md={5}>
                                                        <h5 className={'color-initial mb-1'}>Name</h5>
                                                        <p className={'mb-0 font-size-14'}>Location • Type</p>
                                                    </Col>
                                                    <Col md={7} className={'d-flex justify-content-between font-weight-700 align-items-end'}>
                                                        <div className={'text-center px-2'}>
                                                            <Compo.Icon type={'fal'} icon={'globe'}/>
                                                            <p className={'font-size-12 mb-0'}>Visit Website</p>
                                                        </div>
                                                        <div className={'text-center px-2'}>
                                                            <Compo.Icon type={'fal'} icon={'map-marked'}/>
                                                            <p className={'font-size-12 mb-0'}>Open Map</p>
                                                        </div>
                                                    </Col>
                                                </Row>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                ))
                            }
                        </Row>
                    </Col>
                    <Col md={12}>
                        <h4>Bus Stations</h4>
                        <Row>
                            {
                                range(9).map(index => (
                                    <Col md={4} className={'mb-3'}>
                                        <Card>
                                            <Card.Body className={'p-3'}>
                                                <Row>
                                                    <Col md={5}>
                                                        <h5 className={'color-initial mb-1'}>Name</h5>
                                                        <p className={'mb-0 font-size-14'}>Location • Type</p>
                                                    </Col>
                                                    <Col md={7} className={'d-flex justify-content-between font-weight-700 align-items-end'}>
                                                        <div className={'text-center px-2'}>
                                                            <Compo.Icon type={'fal'} icon={'globe'}/>
                                                            <p className={'font-size-12 mb-0'}>Visit Website</p>
                                                        </div>
                                                        <div className={'text-center px-2'}>
                                                            <Compo.Icon type={'fal'} icon={'map-marked'}/>
                                                            <p className={'font-size-12 mb-0'}>Open Map</p>
                                                        </div>
                                                    </Col>
                                                </Row>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                ))
                            }
                        </Row>
                    </Col>
                </Row>
            </>
        )
    }
}

export default Transportation
