import {
    init_modal,
    open_modal,
    close_modal
} from "../constants";

export const initialState = {}

export default function form(state = initialState, action) {
    switch (action.type) {
        case init_modal:
            return {
                ...state,
                [action.name]: {
                    show: false
                }
            }

        case open_modal:
            return {
                ...state,
                [action.name]: {
                    show: true
                }
            }

        case close_modal:
            return {
                ...state,
                [action.name]: {
                    show: false
                }
            }

        default:
            return state
    }
}
