import React from "react";
import * as Compo from "../../components";
import {Col, Row} from "react-bootstrap";
import Fade from 'react-reveal/Fade';

import projects from '../../data/projects.json'
import locations from '../../data/locations.json'
import studios from '../../data/studios.json'

import HomeCarousel from "../../components/compo/HomeCarousel";
import ProjectCard from "../components/cards/ProjectCard";
import LocationCard from "../components/cards/LocationCard";
import {makeChunk} from "../../helpers/utility";
import StudioCard from "../components/cards/StudioCard";

class Home extends React.Component {

    render () {
        return (
            <>
                <Compo.MainSearchArea/>
                <HomeCarousel/>

                <Fade>
                    <Compo.BodyContent
                        title={'Inspirational Work of The Month'}
                    >
                        <Compo.Slider
                            arrows={true}
                            dots={true}
                            className={'secondary-carousel'}
                        >
                            {
                                makeChunk(projects, 4).map(items => (
                                    <div className={'px-3'}>
                                        <Row>
                                            {
                                                items.map(item => (
                                                    <Col md={3} className="mb-3 px-2">
                                                        <ProjectCard item={item}/>
                                                    </Col>
                                                ))
                                            }
                                        </Row>
                                    </div>
                                ))
                            }
                        </Compo.Slider>
                    </Compo.BodyContent>
                </Fade>

                <Fade>
                    <Compo.BodyContent
                        title={'Popular Locations'}
                    >
                        <Compo.Slider
                            arrows={true}
                            dots={true}
                            className={'secondary-carousel'}
                        >
                            {
                                makeChunk(locations, 4).map(items => (
                                    <div className={'px-3'}>
                                        <Row>
                                            {
                                                items.map(item => (
                                                    <Col md={3} className="mb-3 px-2">
                                                        <LocationCard item={item}/>
                                                    </Col>
                                                ))
                                            }
                                        </Row>
                                    </div>
                                ))
                            }
                        </Compo.Slider>
                    </Compo.BodyContent>
                </Fade>

                <Fade>
                    <Compo.BodyContent
                        title={'Popular Studios'}
                    >
                        <Compo.Slider
                            arrows={true}
                            dots={true}
                            className={'secondary-carousel'}
                        >
                            {
                                makeChunk(studios, 4).map(items => (
                                    <div className={'px-3'}>
                                        <Row>
                                            {
                                                items.map(item => (
                                                    <Col md={3} className="mb-3 px-2">
                                                        <StudioCard item={item}/>
                                                    </Col>
                                                ))
                                            }
                                        </Row>
                                    </div>
                                ))
                            }
                        </Compo.Slider>
                    </Compo.BodyContent>
                </Fade>
            </>
        )
    }
}


export default Home
