import React from "react";
import {Modal, Row} from "react-bootstrap";
import {closeModal, initModals} from "../../../../services/actions/modal";
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import * as Compo from "../../../../components/index";
import {Field, reduxForm} from "redux-form";
import SocialLogin from "./SocialLogin";
import {getData} from "../../../../services/actions/data";

class Registration extends React.Component {

    render () {
        const { handleSubmit, submitting, pristine} = this.props;
        return (
            <Modal
                show={this.props.modal.show}
                onHide={() => this.props.closeModal('registration')}
                backdrop={'static'}
                className={'auth-modal'}
                centered
            >
                <Modal.Header closeButton>
                    <p className={'mb-0'}>Welcome To Nineoneconnect</p>
                    <Modal.Title>Signup</Modal.Title>
                    <p className={'mb-0'}>By continuing, you agree to our <Link>User Agreement</Link> and <Link>Privacy Policy</Link></p>
                </Modal.Header>
                <Modal.Body>
                    <form onSubmit={handleSubmit}>
                        <Field
                            name="name"
                            component={Compo.Field.Input}
                            type="text"
                            placeholder="Enter Your Full Name"
                            label={"Name"}
                        />

                        <Field
                            name="country"
                            component={Compo.Field.Select}
                            placeholder="Select Country"
                            label={"Country"}
                            isClearable={true}
                            options={this.props?.all_countries}
                            option_label_key={'name'}
                            option_value_key={'id'}
                        />

                        <Field
                            name="type"
                            component={Compo.Field.Select}
                            placeholder="Select Account Type"
                            label={"Account Type"}
                            isClearable={true}
                            options={[
                                {label: 'Individual', value: 'individual'},
                                {label: 'Organization', value: 'organization'}
                            ]}
                        />

                        <Field
                            name="email"
                            component={Compo.Field.Input}
                            type="email"
                            placeholder="Enter Your Email"
                            label={"Email"}
                        />

                        <Field
                            name="phone"
                            component={Compo.Field.Input}
                            type="text"
                            placeholder="Enter Your Phone Number"
                            label={"Phone"}
                        />

                        <Field
                            name="website"
                            component={Compo.Field.Input}
                            type="text"
                            placeholder="Enter Your Website"
                            label={"Website"}
                        />

                        <Field
                            name="username"
                            component={Compo.Field.Input}
                            type="text"
                            placeholder="Enter Your Username"
                            label={"Username"}
                        />

                        <Field
                            name="password"
                            component={Compo.Field.Input}
                            type="password"
                            placeholder="Enter Your Password"
                            label={"Password"}
                        />

                        <Field
                            name="confirm_password"
                            component={Compo.Field.Input}
                            type="password"
                            placeholder="Enter Your Password Again"
                            label={"Confirm Password"}
                        />

                        <Compo.Button
                            className={'mb-4 mt-4'}
                            type={'submit'}
                            is_loading={submitting}
                            disabled={pristine || submitting}
                            block
                        >
                            Login
                        </Compo.Button>
                    </form>

                    <Row className={'d-block'}>
                        <div className="text-divider">or</div>
                    </Row>

                    <div className={'text-center mt-3'}>
                        <strong className={'font-family-nunito font-size-14'}>Continue With Other Accounts</strong>
                    </div>

                    <SocialLogin/>

                </Modal.Body>
                <Modal.Footer className={'text-center'}>
                    <span className={'mr-2 font-size-14'}>New to Nineoneconnect?</span>
                    <Compo.Button
                        variant={'link'}
                        style={{
                            "padding": "0",
                            "margin": "0",
                            "font-size": "14px"
                        }}
                    >
                        SIGNUP
                    </Compo.Button>
                </Modal.Footer>
            </Modal>
        )
    }
}

Registration = reduxForm({
    form: 'registration'
})(Registration)


const stateToProps = (state) => ({
    modal: state.modal['registration'],
    all_countries: state.data['all_countries']?.dataset?.data
})

const dispatchToProps = (dispatch) => ({
    initModals: (names) => dispatch(initModals(names)),
    closeModal: (name) => dispatch(closeModal(name)),
    getData: (...props) => dispatch(getData(...props))
})

export default connect(stateToProps, dispatchToProps)(Registration)
