import {
    get_dataset,
    get_data,
    //reset_dataset,
    //reset_data,
    data_loading,
    show_loader,
    hide_loader
} from "../constants";
import {get, post} from "../../helpers/api";


export const getData = (host, endpoint, data, data_name, method='post', multiple=true, loader=true, callback=undefined) => {
    return function(dispatch){

        dispatch({
            type: data_loading,
            name: data_name,
            loading: true
        })

        if(loader) {
            dispatch({
                type: show_loader
            })
        }

        if(method.toUpperCase() === 'GET') {
            get(host, endpoint).then(response => {
                if(multiple){
                    dispatch({
                        type: get_dataset,
                        name: data_name,
                        data: response?.data
                    })
                } else {
                    dispatch({
                        type: get_data,
                        name: data_name,
                        data: response?.data
                    })
                }

                if(callback){
                    callback(response)
                }


                if(loader) {
                    dispatch({
                        type: hide_loader
                    })
                }
            })
        } else if(method.toUpperCase() === 'POST') {
            post(host, endpoint, data).then(response => {
                if(multiple){
                    dispatch({
                        type: get_dataset,
                        name: data_name,
                        data: response?.data
                    })
                } else {
                    dispatch({
                        type: get_data,
                        name: data_name,
                        data: response?.data
                    })
                }

                if(callback){
                    callback(response)
                }

                if(loader) {
                    dispatch({
                        type: hide_loader
                    })
                }
            })
        }
    }
}
