import React from "react";

// Helper Functions
import {range} from "../../../../helpers/utility";

// Custom Components
import * as Compo from "../../../../components";

// React Bootstrap Elements
import {Card, Image, Media} from "react-bootstrap";

// Dropdown Menu Elements
import {default as DrpDown} from "react-bootstrap/Dropdown";
import Dropdown from "../../../../components/compo/Dropdown";

class Recommenders extends React.Component {

    render () {
        return (
            <>
                <h3 className={'p-3 mb-0 color-gray'}>Common Connections</h3>
                {
                    range(3).map(i => {
                        return (
                            <Media className="p-3">
                                <Image src={'https://i.pinimg.com/originals/51/f6/fb/51f6fb256629fc755b8870c801092942.png'} roundedCircle thumbnail fluid width={60} className={'mr-3 shadow'}/>
                                <Media.Body>
                                    <div className={'d-flex'}>
                                        <span>
                                            <h4 className={'mb-0'}>
                                                User Name • <span className={'color-mariner'}>5M <Compo.Icon type={'fas'} icon={'badge-check'}/></span>
                                            </h4>
                                            <p>Position</p>
                                            <p>21 March 2020</p>
                                        </span>
                                        <div className={'ml-auto'}>
                                            <Dropdown
                                                actions={[
                                                    {name: 'Show', action: () => this.alertOne('Show')},
                                                    {name: 'Hide', action: () => this.alertOne('Hide')}
                                                ]}
                                            >
                                                <Compo.Icon
                                                    type={'fas'}
                                                    icon={'ellipsis-v'}
                                                />

                                                <DrpDown.Menu>
                                                    <DrpDown.Item onClick={() => alert('Show')}>Show</DrpDown.Item>
                                                    <DrpDown.Item onClick={() => alert('Hide')}>Hide</DrpDown.Item>
                                                </DrpDown.Menu>
                                            </Dropdown>
                                        </div>
                                    </div>
                                </Media.Body>
                            </Media>
                        )
                    })
                }

                <Card.Footer className={'text-center border-bottom bg-transparent'}>
                    <Compo.Button variant={'outline-primary'}>
                        <span className={'mr-2'}>
                            <Compo.Icon type={'fas'} icon={'plus'}/>
                        </span>
                        Load More
                    </Compo.Button>
                </Card.Footer>

                <h3 className={'p-3 mb-0 color-gray'}>Others</h3>
                {
                    range(10).map(i => {
                        return (
                            <Media className="p-3">
                                <Image src={'https://i.pinimg.com/originals/51/f6/fb/51f6fb256629fc755b8870c801092942.png'} roundedCircle thumbnail fluid width={60} className={'mr-3 shadow'}/>
                                <Media.Body>
                                    <div className={'d-flex'}>
                                    <span>
                                        <h4 className={'mb-0'}>
                                            User Name • <span className={'color-mariner'}>5M <Compo.Icon type={'fas'} icon={'badge-check'}/></span>
                                        </h4>
                                        <p>Position</p>
                                        <p>21 March 2020</p>
                                    </span>
                                        <div className={'ml-auto'}>
                                            <Dropdown
                                                actions={[
                                                    {name: 'Show', action: () => this.alertOne('Show')},
                                                    {name: 'Hide', action: () => this.alertOne('Hide')}
                                                ]}
                                            >
                                                <Compo.Icon
                                                    type={'fas'}
                                                    icon={'ellipsis-v'}
                                                />

                                                <DrpDown.Menu>
                                                    <DrpDown.Item onClick={() => alert('Show')}>Show</DrpDown.Item>
                                                    <DrpDown.Item onClick={() => alert('Hide')}>Hide</DrpDown.Item>
                                                </DrpDown.Menu>
                                            </Dropdown>
                                        </div>
                                    </div>
                                </Media.Body>
                            </Media>
                        )
                    })
                }

                <Card.Footer className={'text-center border-bottom bg-transparent'}>
                    <Compo.Button variant={'outline-primary'}>
                        <span className={'mr-2'}>
                            <Compo.Icon type={'fas'} icon={'plus'}/>
                        </span>
                        Load More
                    </Compo.Button>
                </Card.Footer>
            </>
        )
    }
}


export default Recommenders
