import {
    init_modal,
    open_modal,
    close_modal
} from "../constants";


export const initModal = (name, callback=undefined) => {
    if (callback instanceof Function) {
        callback(name)
    }
    return {
        type: init_modal,
        name: name
    }
}

export const initModals = (names, callback=undefined) => {
    if (callback instanceof Function) {
        callback(names)
    }

    return function(dispatch){
        if (Array.isArray(names)){
            names.forEach(name => {
                dispatch(initModal(name))
            })
        }
    }
}

export const closeModal = (name, callback=undefined) => {
    if (callback instanceof Function) {
        callback(name)
    }

    return {
        type: close_modal,
        name: name
    }
}

export const openModal = (name, callback=undefined) => {
    if (callback instanceof Function) {
        callback(name)
    }

    return {
        type: open_modal,
        name: name
    }
}
