import React from "react";
import {connect} from "react-redux";
import * as Compo from "../../../../components/index";
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props'
import GoogleLogin from 'react-google-login';
import { LinkedIn } from 'react-linkedin-login-oauth2';
import { toast } from 'react-toastify';
import {
    facebookLogin,
    googleLogin,
    linkedinLogin
} from "../../../../services/actions/auth";

class SocialLogin extends React.Component {
    render () {
        return (
            <div className="mt-3 d-flex justify-content-center">
                <span className={'mx-2'}>
                    <GoogleLogin
                        clientId={'360629451766-fkt5a1316bkiqo8eafsd2hk9fbqe7cea.apps.googleusercontent.com'}
                        render={renderProps => (
                            <Compo.Button
                                variant={'link'}
                                style={{
                                    "border": "1px solid var(--mariner)",
                                    "padding": "0",
                                    "height": "40px",
                                    "width": "40px",
                                    "background-color": "transparent",
                                    "color": "#E34133"
                                }}
                                onClick={renderProps.onClick}
                                disabled={renderProps.disabled}
                            >
                                <Compo.Icon type={'fab'} icon={'google'}/>
                            </Compo.Button>
                        )}
                        onSuccess={data => this.props.googleLogin(data)}
                        onFailure={data => console.log(data)}
                    />
                </span>
                <span className={'mx-2'}>
                    <FacebookLogin
                        appId="4043311352425203"
                        fields="name,email,picture"
                        callback={data => this.props.facebookLogin(data)}
                        render={renderProps => (
                            <Compo.Button
                                onClick={renderProps.onClick}
                                variant={'link'}

                                style={{
                                    "border": "1px solid var(--mariner)",
                                    "padding": "0",
                                    "height": "40px",
                                    "width": "40px",
                                    "background-color": "transparent",
                                    "color": "#4064AC"
                                }}
                            >
                                <Compo.Icon type={'fab'} icon={'facebook-f'}/>
                            </Compo.Button>
                        )}
                    />
                </span>
                <span className={'mx-2'}>
                    <LinkedIn
                        clientId="861cwu8ot5gkep"
                        onFailure={data => toast.error('Something Went Wrong, Can\'t Login With Linkedin.')}
                        onSuccess={data => this.props.linkedinLogin(data)}
                        redirectUri={'http://localhost:3000/auth/login/linkedin'}
                        scope={'r_emailaddress r_liteprofile'}
                    >
                        <Compo.Button
                            variant={'link'}
                            style={{
                                "border": "1px solid var(--mariner)",
                                "padding": "0",
                                "height": "40px",
                                "width": "40px",
                                "background-color": "transparent",
                                "color": "#0B74B1"
                            }}
                        >
                            <Compo.Icon type={'fab'} icon={'linkedin-in'}/>
                        </Compo.Button>
                    </LinkedIn>
                </span>
            </div>
        )
    }
}


const stateToProps = (state) => ({})

const dispatchToProps = (dispatch) => ({
    facebookLogin: (data) => dispatch(facebookLogin(data)),
    linkedinLogin: (data) => dispatch(linkedinLogin(data)),
    googleLogin: (data) => dispatch(googleLogin(data))
})

export default connect(stateToProps, dispatchToProps)(SocialLogin)
