import {
    get_dataset,
    get_data,
    reset_dataset,
    reset_data,
    data_loading
} from "../constants";

export const initialState = {}

export default function form(state = initialState, action) {
    switch (action.type) {
        case get_dataset:
            return {
                ...state,
                [action.name]: {
                    ...state[action.name],
                    dataset: action.data,
                    loading: false
                }
            }

        case get_data:
            return {
                ...state,
                [action.name]: {
                    ...state[action.name],
                    data: action.data,
                    loading: false
                }
            }

        case reset_dataset:
            return {
                ...state,
                [action.name]: {
                    ...state[action.name],
                    data: [],
                }
            }

        case reset_data:
            return {
                ...state,
                [action.name]: {
                    ...state[action.name],
                    data: {}
                }
            }

        case data_loading:
            return {
                ...state,
                [action.name]: {
                    ...state[action.name],
                    loading: action.loading
                }
            }

        default:
            return state
    }
}
