import {post} from "./api";
import {SubmissionError} from 'redux-form'

export const processData = (data) => {
    let form_data = new FormData()
    for (const [field, value] of Object.entries(data)) {
        if (typeof value === 'string' || typeof value === 'number' || typeof value === 'boolean') {
            setOrAppendFormData(form_data, field, value)
        } else if (typeof value === 'object' || Array.isArray(value)) {
            buildObjectFormData(form_data, value, field)
        }
    }
    return form_data
}

const buildObjectFormData = (form_data, data, field) => {
    if (Array.isArray(data)) {
        data.forEach((item, index) => {
            const build_field = `${field}[${index}]`
            buildObjectFormData(form_data, item, build_field)
        })
    } else {
        if(typeof data?.lastModified === 'number' || typeof data === 'string' || typeof data === 'number' || typeof data === 'boolean') {
            setOrAppendFormData(form_data, field, data)
        } else if (data !== undefined) {
            for (const [key, value] of Object.entries(data)) {
                const build_field = `${field}[${key}]`
                if (typeof value === 'string' || typeof value === 'number' || typeof value === 'boolean') {
                    setOrAppendFormData(form_data, build_field, value)
                } else if (typeof value === 'object' || Array.isArray(value)) {
                    buildObjectFormData(form_data, value, build_field)
                }
            }
        }
    }
}

const setOrAppendFormData = (form_data, field, data) => {
    if(!form_data.has(field)){
        form_data.append(field, data)
    } else {
        form_data.set(field, data)
    }
}

export const submitForm = (host, endpoint, values, callback) => {
    const form_data = processData(values)
    return post(host, endpoint, form_data).then(res => {

        if (typeof callback === 'function') {
            callback(res)
        }

        if (res?.status_code === 406 && Object.entries(res.errors).length > 0) {
            throw new SubmissionError(res.errors)
        }
    })
}
