import React from "react";
import {Form} from "react-bootstrap";
import {default as Slect} from "react-select";

export const Select = ({
                           input, input: {value, onChange, onBlur},
                           name,
                           label,
                           options,
                           placeholder,
                           isMulti,
                           meta: {touched, error},
                           option_label_key = 'label',
                           option_value_key = 'value',
                           isClearable = false
                       }) => {
    const final_options = options && options.map(item => (
        {
            label: item[option_label_key],
            value: item[option_value_key]
        }
    ))
    let default_value = final_options && final_options.find(item => item.value === input.value)

    return (
        <>
            <Form.Group>
                {label && <label className="form-label">{label}</label>}
                <Slect
                    {...input}
                    name={name}
                    isMulti={isMulti}
                    isClearable={isClearable}
                    options={final_options}
                    placeholder={placeholder}
                    value={value && default_value}
                    onBlur={() => {
                        onBlur(value)
                    }}
                    onChange={(event) => {
                        if (isMulti) {
                            onChange(event.map(item => item[option_value_key]))
                        } else {
                            onChange(event?.value)
                        }
                    }}
                />
                {touched && error && <Form.Text className="text-danger">{error}</Form.Text>}
            </Form.Group>
        </>
    )
}
